.sb-rendition_elevated {
  box-shadow: var(--sbt-elevation-20);
  position: relative;
}

.sb-rendition_full-size {
  height: 100%;
  overflow-y: auto;
}

.sb-rendition_border-rounded {
  border-radius: var(--sbt-border-radius-large);
  overflow: hidden;
}

.sb-rendition_border-top {
  border-top: 1px solid var(--sbt-theme-tertiary-color-20);
}

.sb-rendition_border-right {
  border-right: 1px solid var(--sbt-theme-tertiary-color-20);
}

.sb-rendition_border-bottom {
  border-bottom: 1px solid var(--sbt-theme-tertiary-color-20);
}

.sb-rendition_border-left {
  border-left: 1px solid var(--sbt-theme-tertiary-color-20);
}
